@media (min-width: 800px) {
  .project-title::after {
    width: 50%;
    top: 70%;
    left: -30px;
  }

  .project-flex-container{

    flex-direction: row;
    justify-content: space-around;
  }

  .project-info-container,
  .project-image-container {
    width: 47.5%;
  }

  .about-flex-container{
    flex-direction: row;
  }

  .about-body-container{
    width: 60%;
  }

  .cv-text{
    width: 40%;
  }
}