/* ================= Navigation =================== */

.header {
  width: 100%;
  position: fixed;
  z-index: 950;
  background-color: var(--clr-dark);
  height: 75px;
  display: flex;
  place-content: center;
}

.header-content-wrapper{
  width: 95%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav {
  position: fixed;
  top: 75px;
  bottom: 50%;
  right: 0;
  left: 60%;
  z-index: 900;
  background: var(--clr-dark);
  transform: translateY(-140%);
  opacity: 0;
  transition: opacity 500ms ease;
}

.nav-logo {
  width: 30px;
  height: 30px;
  margin-left: 1em;
  padding: 0;
}

.nav-open .nav,
.sticky .header {
  border-bottom: 3px solid var(--clr-highlight);
  background-color: var(--clr-dark);
  -webkit-box-shadow: 6px 19px 17px -15px rgba(38, 38, 38, 0.67);
  -moz-box-shadow: 6px 19px 17px -15px rgba(38, 38, 38, 0.67);
  box-shadow: 6px 19px 17px -15px rgba(38, 38, 38, 0.67);
}

.logo-nav {
  width: 100%;
}

.nav-list {
  list-style: none;
  display: flex;
  flex-direction: column;
}

.nav-list {
  justify-content: space-evenly;
  align-items: center;
  height: 100%;
}

.nav-link {
  color: var(--clr-light);
  text-decoration: none;
  font-size: 1.5rem;
  font-weight: 700;
}

.sticky .nav-link {
  color: var(--clr-light);
}

.nav-link:hover {
  color: var(--clr-highlight)
}


.active {
  color: var(--clr-highlight);
}

.menu-icon  {
  width:2rem;
  height: 20px;
  cursor: pointer;
  background: transparent;
  padding: 0; 
  position: relative;
  right: 1em;
  z-index: 1000;
}

.hamburger {
  display: block;
  position: absolute;
  top: 50%;
  right: 0;
}

.hamburger::after,
.hamburger::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;

}

.hamburger,
.hamburger::after,
.hamburger::before {
  background-color: var(--clr-light);
  height: 4px;
  width: 2rem;
  border-radius: .5em;
  transition: transform 250ms ease-in-out;
}

.sticky .hamburger,
.sticky .hamburger::after,
.sticky .hamburger::before {
  background-color: var(--clr-light);
}


.nav-open .hamburger,
.nav-open .hamburger::after,
.nav-open .hamburger::before {
  background-color: var(--clr-light);
}

.hamburger::after {
  top: 8px;
}

.hamburger::before {
  bottom: 8px;
}



.nav-open .nav {
  transform: translateY(0);
  opacity: 1;
}

.nav-open .menu-icon  {
  position: fixed;
}

.nav-open .hamburger {
  transform: rotate(135deg)
}

.nav-open .hamburger::before {
  opacity: 0;
}

.nav-open .hamburger::after {
  transform: rotate(90deg) translateX(-8px)
}

.nav-open .nav-list{
  padding: 0;
}


/* =============== FOOTER =============== */


.footer {
  width: 100%;
  overflow: hidden;
  background-color: #1a1a2e;
  color: rgba(255, 255, 255, 0.6);
  padding: 2em 0 0;
  min-height: 30vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  letter-spacing: 1px;
}

.footer-nav-container {
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  padding: 1em 0 0;
}

.footer-nav-wrapper {
  display: flex;
  justify-content: space-evenly;
  border-bottom: 1px solid var(--clr-light);
  padding: 2em 0;
}

.footer-social-media-container{
  padding: 2em 0;
}

.footer-nav-list {
  list-style: none;
  padding: 0;
  display: flex;
  flex-direction: row;  
  flex-wrap: wrap;
  text-align: center;
}

.footer-nav-list>li {
  padding: 1em;
  display: inline;

}

.footer-nav-list>li:last-of-type {
  padding-bottom: 0;
}

.footer-social-media-container{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.footer-nav-list-icons{
  display: flex;
  padding: 2em 0 0;
  place-content: center;
  list-style: none;

}

.footer-nav-list-icons > li{
  padding: 0 2em;
}

.footer .active {
  color: var(--clr-light);
}

.copyright {
  font-size: 0.8em;
  text-align: center;
  padding: 5em 0;
  border-top: 1px solid #4a4949;
  background-color: rgb(30, 30, 30);

}

.footer-nav-link {
  color: var(--clr-light);
  text-decoration: none;
  text-align: left;
}

.footer-nav-link:hover,
.footer-nav-link:focus {
  color: var(--clr-hl);
  text-decoration: underline;
}

.footer-social-icon {
  width: 20px;
}

/* ============ MEDIA QUERIES ============ */

@media (min-width: 1100px) {
  .hero-content-wrapper{
    max-width: 1100px;
    margin: 0 auto;
  }
}

@media (min-width: 800px) {

  .header {
    align-items: center;
  }

  .menu-icon  {
    display: none;
  }

  .nav {
    background: transparent;
    /* display: block; */
    /* height: inherit; */
    position: static;
    transform: translateX(0%);
    transition: 300ms ease-in-out;
    padding: 0 3em 0 1em;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    opacity: 1;
  }

  .nav-open .nav,
.sticky .header {
  border-bottom: none;
}
  
  .nav-logo {
    /* max-width: 50px; */
    margin-left: 3em;
  }

  .nav-list {
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    /* height: inherit; */
    margin: 0;
  }

  .nav-link {
    color: var(--clr-light);
    text-decoration: none;
    /* font-size: 2rem; */
    font-weight: 700;
    padding: 0 1em;
  }

  .footer {
    font-size: 0.7em;
  }

  .footer-nav-container {
    width: 100vw;
    flex-direction: row;
    justify-content: space-around;
    padding: 1em 0 3em;
  }

  .footer-nav-wrapper{
    width: 50%;
    border: none;
  }

}