@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;700&family=Montserrat:wght@400;500;700&family=Work+Sans:ital,wght@0,300;0,400;1,500&display=swap');
:root {
  --clr-light: #e8e8e8;
  --clr-dark: #1d1c50;
  --clr-highlight: #dd7702;
  
  font-family: "Inter", "Avenir", Helvetica, Arial, sans-serif;
  font-size: 62.5%;
  line-height: 24px;
  font-weight: 400;

  color-scheme: light dark;
  color: var(--clr-dark);
  background-color: var(--clr-light);

  font-synthesis: none;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;

  scroll-behavior: smooth;
  
}

*,
*::after,
*::before {
  box-sizing: border-box;
  /* outline: 1px solid #9ef7a5; */
}

html,
body,
h1,p {
  margin: 0;

}

body {
  min-width: 100vw;
  min-height: 100vh;
  font-size: 1.6rem;
}

