/* ====================== PORTFOLIO ====================== */

.portfolio {
    padding: 5em 1.5em;
    font-family: 'Work Sans', sans-serif;
    font-weight: 300;
  }
  
  .project {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  
  .project-flex-container{
    display: flex;
    flex-direction: column;
  }
  
  .project-info-container,
  .project-image-container {
    width: 90%;
    max-width: 35em;
    align-self: center;
  }
  
  .project-title {
    font-size: 2.5em;
    line-height: 1.2;
    position: relative;
    display: inline-block;
  }
  
  .project-title::after {
    content: "";
    width: 100%;
    height: 15px;
    background-color: var(--clr-highlight);
    display: block;
    position: absolute;
    top: 90%;
    z-index: -1;
  }
  
  .project-description {
    width: 90%;
    font-size: 1.5em;
    margin: 0.5em 0.001em 1.5em 0;
  }
  
  
  .project-image {
    width: 100%;
    border-radius: 12px;
    margin: 0 auto;
  }
  
  .project-image:hover,
  .project-image:focus {
    -webkit-box-shadow: 14px 22px 11px 3px rgba(123, 118, 118, 0.75);
    -moz-box-shadow: 14px 22px 11px 3px rgba(123, 118, 118, 0.75);
    box-shadow: 14px 22px 11px 3px rgba(123, 118, 118, 0.75);
    transition: box-shadow 400ms ease-in-out;
  }
  
  
  /* ====================== ABOUT ====================== */
  .about-flex-container{
    display: flex;
    flex-direction: column;
  }
  .about-body{
    margin-bottom: 1.5em;
    max-width: 550px;
  }
  
  .cv-text{
    font-size: 1.5em;
    padding: 1em 0;
  }
  
  .about-highlight a{
    color: var(--clr-highlight);
    font-weight: 700;
    cursor: pointer;
    text-decoration: none;
  }
  
  .about-highlight a:active,
  .about-highlight a:hover{
    color: var(--clr-dark);
    text-decoration: underline currentColor;
  }
  
  .profile-image{
    width: 90%;
    max-width: 200px;
  }
  
  .stack-container {
    font-size: 0.7em;
    padding: 0;
    margin: 2em 0;
    text-align: left;
  }
  
  .stack-list-item{
    list-style: none;
    display: inline-block;
    background-color: transparent;
    color: var(--clr-dark);
    margin: 0 0.5em 0.8em;
  }
  
  .stack-image{
    width: 20px;
    margin: 0 auto;
  }
  
  .stack-image img{
    width: 100%;
  }
  
  .skill-name{
    color: var(--clr-dark);
    background-color: transparent ;
    padding: 0 1em;
  }

  .about-me-body{
    padding-bottom: 1em;
  }
  
  .about-me-link{
    display: block;
    text-decoration: none;

    color: var(--clr-highlight);
    font-weight: bolder;

  }
@media (min-width: 600px){
    .profile-image{
        width: 250px;
        object-fit: contain;
      }
      
      .stack-container {
        max-width: 550px;
      }

      .about-me-container{
        display: flex;
        flex-direction: row;
        justify-content: space-around;
      }

      .about-me-info{
        max-width: 550px;
      }
}